t\"/>\n      </template>\n    </van-swipe-cell>\n    <van-swipe-cell v-if=\"user.personalId\">\n      <van-cell :title=\"user.personalId\" label=\"Personal ID\"/>\n      <template #right>\n        <van-button square type=\"danger\" class=\"delete-button\" text=\"Delete\" @click=\"user.reset\"/>\n      </template>\n    </van-swipe-cell>\n  </van-cell-group>\n</template>\n\n<style scoped>\n.van-image {\n  filter: invert(100%);\n}\n</style>\n"],"sourceRoot":""}]);
